.btn-primary {
  background:yellow;
  color: #1f2123;
  width: 100px;
  border: none;
  border-radius: 3px;
  display: block;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
}

.movie-wrapper {
  /* position: relative; */
}

.movie-detail {
  display: flex;
  justify-content: space-between;
  /* position: absolute;
  bottom: 0;
  left: 0;
  top: 0;
  right: 0; */
  color: #fff;
  background: rgba(0,0,0,0.75);
  padding: 30px;
  position: relative;
}

.overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 111;
  padding: 50px;
  background: rgba(0,0,0,0.35);
  display: none;
}

.free-content {
  max-height: 400px;
  overflow-y: scroll;
}

.movie-detail-image {
  margin-right: 30px;
}

button.modal-video-close-btn.modal-close {
  right: 15px;
  top: 15px;
  cursor: pointer;
  transform: rotate(0deg);
  transition: all 0.5s;
}

button.modal-video-close-btn:hover {
  transform: translate(-5px) rotate(90deg);
  transition: all 0.5s;
}


@media screen and (max-width: 900px) {
  .movie-detail {
    display: block;
  }
  .movie-detail-image {
      margin-right: 0;
      text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .app {
    padding: 4rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
  .overlay {
    padding: 0;
    overflow: scroll;
  }
  .movie-detail {
    padding: 50px 30px;
  }
  .movie-detail-image img {
      max-width: 70%;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }

  .movie {
    width: "100%";
    margin: 1rem;
  }
}
